const actions = {
  GET_SUPPLIER_TYPES_REQUEST: "GET_SUPPLIER_TYPES_REQUEST",
  GET_SUPPLIER_TYPES_SUCCESS: "GET_SUPPLIER_TYPES_SUCCESS",
  STORE_SUPPLIER_TYPE_REQUEST: "STORE_SUPPLIER_TYPE_REQUEST",
  STORE_SUPPLIER_TYPE_SUCCESS: "STORE_SUPPLIER_TYPE_SUCCESS",
  STORE_SUPPLIER_TYPE_ERROR: "STORE_SUPPLIER_TYPE_ERROR",
  EDIT_SUPPLIER_TYPE_REQUEST: "EDIT_SUPPLIER_TYPE_REQUEST",
  EDIT_SUPPLIER_TYPE_SUCCESS: "EDIT_SUPPLIER_TYPE_SUCCESS",
  EDIT_SUPPLIER_TYPE_ERROR: "EDIT_SUPPLIER_TYPE_ERROR",
  UPDATE_SUPPLIER_TYPE_REQUEST: "UPDATE_SUPPLIER_TYPE_REQUEST",
  UPDATE_SUPPLIER_TYPE_SUCCESS: "UPDATE_SUPPLIER_TYPE_SUCCESS",
  UPDATE_SUPPLIER_TYPE_ERROR: "UPDATE_SUPPLIER_TYPE_ERROR",
  SHOW_SUPPLIER_TYPE_REQUEST: "SHOW_SUPPLIER_TYPE_REQUEST",
  SHOW_SUPPLIER_TYPE_SUCCESS: "SHOW_SUPPLIER_TYPE_SUCCESS",
  SHOW_SUPPLIER_TYPE_ERROR: "SHOW_SUPPLIER_TYPE_ERROR",
  DEACTIVATE_SUPPLIER_TYPE_REQUEST: "DEACTIVATE_SUPPLIER_TYPE_REQUEST",
  DEACTIVATE_SUPPLIER_TYPE_SUCCESS: "DEACTIVATE_SUPPLIER_TYPE_SUCCESS",
  DEACTIVATE_SUPPLIER_TYPE_ERROR: "DEACTIVATE_SUPPLIER_TYPE_ERROR",
  ACTIVATE_SUPPLIER_TYPE_REQUEST: "ACTIVATE_SUPPLIER_TYPE_REQUEST",
  ACTIVATE_SUPPLIER_TYPE_SUCCESS: "ACTIVATE_SUPPLIER_TYPE_SUCCESS",
  ACTIVATE_SUPPLIER_TYPE_ERROR: "ACTIVATE_SUPPLIER_TYPE_ERROR",
  CLEAR_SUCCESS: "CLEAR_SUCCESS",
  CLEAR_ERROR: "CLEAR_ERROR",
  CLEAR_ERROR_FIELD: "CLEAR_ERROR_FIELD",
  getSupplierTypes: (page, search) => ({
    type: "GET_SUPPLIER_TYPES_REQUEST",
    payload: {
      page: page,
      search: search
    }
  }),
  storeSupplierType: data => ({
    type: "STORE_SUPPLIER_TYPE_REQUEST",
    payload: { body: data }
  }),
  editSupplierType: id_supplier_type => ({
    type: "EDIT_SUPPLIER_TYPE_REQUEST",
    payload: { id_supplier_type: id_supplier_type }
  }),
  updateSupplierType: (id_supplier_type, data) => ({
    type: "UPDATE_SUPPLIER_TYPE_REQUEST",
    payload: {
      id_supplier_type: id_supplier_type,
      body: data
    }
  }),
  showSupplierType: id_supplier_type => ({
    type: "SHOW_SUPPLIER_TYPE_REQUEST",
    payload: { id_supplier_type: id_supplier_type }
  }),
  deactivateSupplierType: id_supplier_type => ({
    type: "DEACTIVATE_SUPPLIER_TYPE_REQUEST",
    payload: { id_supplier_type }
  }),
  activateSupplierType: id_supplier_type => ({
    type: "ACTIVATE_SUPPLIER_TYPE_REQUEST",
    payload: { id_supplier_type }
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  })
};

export default actions;
