import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

import SupplierTypeCreateForm from "./createForm";

import notification from "../../../../components/notification";
// import { Input, Select, Divider } from "antd";

import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/supplier/supplierType/actions";
import { Box } from "../../../App/app.style";

class CreateSupplierType extends Component {
  handleSubmit = value => {
    const { storeSupplierType } = this.props;

    // Uppercasing supplierType code
    // value.supplier_type_code = value.supplier_type_code.toUpperCase();
    // console.log(value);
    storeSupplierType(value);
  };

  componentDidUpdate() {
    // Display error notif
    if (this.props.supplierType.error) {
      notification("error", this.props.supplierType.error);
      this.props.clearError();
    }

    if (this.props.supplierType.success) {
      this.props.history.goBack();
    }
  }

  render() {
    // console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader title={<IntlMessages id="supplierTypes.add.title" />}
            style={{ margin: 0 }}
          >
            <Button onClick={this.props.history.goBack}>
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <SupplierTypeCreateForm
            auth={this.props.auth}
            submit={this.handleSubmit}
            supplierType={this.props.supplierType}
          />
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return { auth: state.auth, supplierType: state.supplierType };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(CreateSupplierType);
